.rightbar {
    flex: 3.5;
}

.rightbarWrapper{
    /* display: flex; */
    padding: 20px 20px 0 0;
}
.birthdayImg{
    width: 40px;
    padding: 40px;
    margin-right: 10px;
}

.birthdayContainer{
    display: flex;
    align-items: center;
}

.birthdayText{
    font-weight: 300;
    font-size: 15px;
}

.righthbarReclama {
    width: 100%;
    border-radius: 10px;
    margin: 30px 0;
}

.rightbarFriendsList{
    padding: 0;
    margin: 0;
    list-style: none;
}

.rightbarFriend{
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}

.rightbarProfileImgContainer{
    margin-right: 10px;
    position: relative;
}

.rightbarProfileImg{
    width: 40px;
    height: 40px;
    border-radius: 50px;
    object-fit: cover;
}

.rightbarOnline {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: rgb(0, 255, 55);
    border: 2px solid rgb(255, 255, 255);
    position: absolute;
    top: -2px;
    right: 0;

}


.rightbarUserName {
    font-weight: 500;
}

.rightbarTitle{
margin-bottom: 20px;
}